/* Navbar.css */

.navbar {
    background-color: #333;
    color: #fff;
    padding: 10px 20px;
  }
  
  .nav-list {
    list-style: none;
    padding: 0;
  }
  
  .nav-item {
    display: inline;
    margin-right: 20px;
  }
  
  .nav-item a {
    text-decoration: none;
    color: #fff;
    font-weight: bold;
  }
  